.InfoPanel {
  -ms-flex-align: center;
      align-items: center;
  background: rgba(180,180,180,.8);
  border-radius: 6px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px;
  padding: 10px;
}

.InfoPanel .player {
  height: 80px;
  width: 36px;
}

.InfoPanel div {
  margin-bottom: 12px;
}

.InfoPanel button {
  background: #fbf;
  border-radius: 0;
  border: 1px solid rgba(33,33,33,.6);
  color: #000;
  cursor: pointer;
  outline: 0;
  padding: 10px 20px;
  -webkit-transition: -webkit-box-shadow .3s;
  transition: -webkit-box-shadow .3s;
  -o-transition: box-shadow .3s;
  transition: box-shadow .3s;
  transition: box-shadow .3s, -webkit-box-shadow .3s;
}

.InfoPanel button:hover {
  -webkit-box-shadow: 0 0 11px rgba(33,33,33,.6);
          box-shadow: 0 0 11px rgba(33,33,33,.6); 
}


